<template>
	<div  class="ConsignmentIndex">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>{{HeadTitle}}</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<span>{{invoiceText}}</span>
				<input  placeholder="" @keyup.enter="getFocus('CommodityCode')"   ref="InvoiceNum"  v-model="InvoiceNum" ></input>
				<i v-if="InvoiceNum" @click="clearInput('InvoiceNum')" class="iconfont icon-cuowu"></i>
			</div>
			<div  class="import">
				<span></span>
				<input  placeholder=""  @keyup.enter="Submit"  ref="CommodityCode"  v-model="CommodityCode"></input>
				<i v-if="CommodityCode" @click="clearInput('CommodityCode')"  class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="toChoiceGoods" ></i>
			</div>
			<div class="g_doubleBtn">
				<div @click="Submit"></div>
				<div @click="ToCheckDetails"></div>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  id="g_tips"   class="g_tips">
			<div>
				<span></span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'></p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

import $ from "jquery"
	export default{
		name:"ConsignmentIndex",
		data(){
			return{
				InvoiceNum:'',//0000003331         0000002929 
				CommodityCode:'',//1010001000   1010002000   
				tipsHide:false,//
				msg:"",//
				inputFocus:"",// input
				thisPage:"Receipt",
				HeadTitle:"",//
				invoiceText:"",// 

			}
		},
		mounted() {
			this.$refs['InvoiceNum'].focus();

			// $(".g_tips").show()

// 			var btn4 = document.getElementById("g_tips");
// btn4.addEventListener("click",function(){
// 	console.log(22)
// });

			// $(".g_tips").addEventListener("DOMAttrModified", function () {

			// 	var gamemuiscs1 = new Audio("static/audio/wrong01.wav");

			//     // function audioAutoPlay() {

			//         gamemuiscs1.play();
			//     // }
			//     // audioAutoPlay();
			// });

		},
		created(){

			var that = this;

			// 
			if(this.$route.query.name){


				if(this.$route.query.name == "Receipt"){
				// 

					that.$store.state.IsConsignOrReceipt = "Receipt" ;
					that.thisPage = "Receipt";

				}else if(this.$route.query.name == "Consignment"){
				// 

					that.$store.state.IsConsignOrReceipt = "Consignment" ;
					that.thisPage = "Consignment";

				}
			}else{
			//  
				if(this.$store.state.IsConsignOrReceipt == "Receipt"){
					
					that.thisPage = "Receipt";

				}else if(this.$store.state.IsConsignOrReceipt == "Consignment"){

					that.thisPage = "Consignment";
				}
			}

			if(this.thisPage == "Receipt"){

				that.HeadTitle = "";
				that.invoiceText = "";

			}else if(this.thisPage == "Consignment"){

				that.HeadTitle = "";
				that.invoiceText = "";
			}


			if(this.$route.query.InvoiceNum){

				this.InvoiceNum = this.$route.query.InvoiceNum;
				this.CommodityCode = this.$route.query.CommodityCode;
			}

		},
		methods:{
			// 
			getFocus:function(input){

				this.$refs[input].focus();
			},
 			// / 
			Submit:function(){
				var that = this;

				if(!this.InvoiceNum ){

					that.tipsShow("CommodityCode");

					that.inputFocus = "InvoiceNum";

					that.msg = "";

					return;
				}else if(!this.CommodityCode ){

					that.tipsShow("CommodityCode")

					that.inputFocus = "CommodityCode";

					that.msg = "";

					return;
				}

				if(this.thisPage == "Receipt"){

					var currentUrl = this.HOST + "RFReceiptSerialCollectSku.do";


				}else if(this.thisPage == "Consignment"){

					var currentUrl = this.HOST + "RFSerialCollectSku.do";

				}

				var postData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						that.$store.state.Consignment_SubmitData = res.data.data;

						//
						that.$router.push({
							name:"ConsignmentNumCollect",
							query:{
							}
						})

					}else{

						that.tipsShow("CommodityCode")

						that.inputFocus = "InvoiceNum";

						that.msg = "";
					}
				},"InvoiceNum")
		    },
		    //   
		    ToCheckDetails:function(){
				
				var that = this;

				if(!this.InvoiceNum ){
					
					that.tipsShow("InvoiceNum")

					that.inputFocus = "InvoiceNum";

					that.msg = "";

					return;
				}

				if(this.thisPage == "Receipt"){

					var CheckDetailsUrl = this.HOST + "RFReceiptSerialCollectQuery.do";


				}else if(this.thisPage == "Consignment"){

					var CheckDetailsUrl = this.HOST + "RFSerialCollectQuery.do";

				}

				var CheckDetailsData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(CheckDetailsUrl,CheckDetailsData,function(res){

					if(res.data.data.length){

						that.$store.state.Consignment_CheckDetailsData = res.data.data;

						//  
						that.$router.push({
							name:"CheckDetails",
							query:{
								InvoiceNum:that.InvoiceNum,//
								CommodityCode:that.CommodityCode,//
							}
						})
					}else{

						that.tipsShow("InvoiceNum")

						that.inputFocus = "InvoiceNum";

						that.msg = "";
					}
				},"InvoiceNum")
		    },
		    //   
		    toChoiceGoods:function(){
		    
				var that = this;

				if(!this.InvoiceNum ){
					
					that.tipsShow("InvoiceNum")

					that.inputFocus = "InvoiceNum";

					that.msg = "";

					return;
				}

				if(this.thisPage == "Receipt"){

					var CollectSkuSelUrl = this.HOST + "RFReceiptSerialCollectSkuSel.do";


				}else if(this.thisPage == "Consignment"){

					var CollectSkuSelUrl = this.HOST + "RFSerialCollectSkuSel.do";

				}

				var CollectSkuData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(CollectSkuSelUrl,CollectSkuData,function(res){

					if(res.data.data.length){

						that.$store.state.Consignment_ChoiceData = res.data.data;

						//  
						that.$router.push({
							name:"ChoiceGoods",
							query:{
								InvoiceNum:that.InvoiceNum,//
								CommodityCode:that.CommodityCode,//
							}
						})

					}else{

						that.tipsShow("InvoiceNum")

						that.inputFocus = "InvoiceNum";

						that.msg = "";
					}
				},"InvoiceNum")
		    }
		}
	}
</script>
<style scoped lang="less">
.ConsignmentIndex{
	&>.container{
		font-size: 30/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div.import{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			box-sizing: border-box;
			position: relative;
			span{
				width: 140/50rem;
				text-align: right;
			}
			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
		}
	}
}
</style>


