<template>
	<div class="CheckDetails">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span></span></header>
		<div class="container">
			<div>
				<span></span>
				<span></span>
				<span></span>
				<span></span>
			</div>
			<div v-for='(dataItem,index) in Consignment_CheckDetailsData'  :id="index"  :key='index'   @click="selectData(index)">
				<span>{{dataItem.SerailNo}}</span>
				<span>{{dataItem.Sku}}</span>
				<span>{{dataItem.CaseNo}}</span>
				<span>{{dataItem.Descr}}</span>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckDetails",
		data(){
			return{
				Consignment_CheckDetailsData:[],//
				title:"",
				detail_item:0,//()
				ifSelect:false,//
			}
		},
		created:function(){

			this.title = this.$route.query.title;

			this.ifSelect = this.$route.query.ifSelect;


			if(this.$store.state.Consignment_CheckDetailsData){

				var Consignment_CheckDetailsData = this.$store.state.Consignment_CheckDetailsData;

				this.Consignment_CheckDetailsData = Consignment_CheckDetailsData;
			}
		},
		methods:{
			//
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.$route.query.InvoiceNum,//
						CommodityCode:that.$route.query.CommodityCode,//
					}
				})
			},
			//
			selectData:function(idx){

				this.detail_item = idx;
			},
			//   
			selectData_submit:function(){

				var that = this;

				this.$router.push({
					name:"TakeDelivery",
					query:{
						Summary:{
							descr:that.Consignment_CheckDetailsData[that.detail_item].descr,//
							qtyreceived:that.Consignment_CheckDetailsData[that.detail_item].qtyreceived,//
							qtyremain:that.Consignment_CheckDetailsData[that.detail_item].qtyremain,//
							sku:that.Consignment_CheckDetailsData[that.detail_item].sku,//
							susr2:that.Consignment_CheckDetailsData[that.detail_item].susr2,//
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.CheckDetails{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:1300/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:260/50rem;
					text-align:center;
				}
				&>span:last-child{
					width:500/50rem;
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
</style>


